<!-- 메뉴 상세화면 -->
<template>
  <div>
    <!-- 이미지 -->
    <svg
      class="bd-placeholder-img card-img-top"
      width="300px"
      height="200"
      xmlns="http://www.w3.org/2000/svg"
      role="img"
      aria-label="Placeholder: Image cap"
      preserveAspectRatio="xMidYMid slice"
      focusable="false"
    >
      <title>Placeholder</title>
      <rect width="100%" height="100%" fill="#868e96" />
      <text x="50%" y="50%" fill="#dee2e6" dy=".3em">Image cap</text>
    </svg>

    <!-- 메뉴 상세 내용 -->
    <p>메뉴 상세 (설명) 내용 <br />{{ menuInfo.finfo }}</p>
    

    <!-- 가격 옵션 -->
    <div class="list-group" v-if="this.menuInfo.foption == 1">
      <label class="list-group-item d-flex gap-2">
        <input
          class="form-check-input flex-shrink-0"
          type="radio"
          name="listGroupRadios"
          id="listGroupRadios1"
          value="0"
          v-model="option"
          checked
        />
        <span>
          기본
          <small class="d-block text-body-secondary">+0원</small>
        </span>
      </label>
      <label class="list-group-item d-flex gap-2">
        <input
          class="form-check-input flex-shrink-0"
          type="radio"
          name="listGroupRadios"
          id="listGroupRadios2"
          value="500"
          v-model="option"
        />
        <span>
          곱빼기
          <small class="d-block text-body-secondary">+500원</small>
        </span>
      </label>
    </div>
    <p>가격 : {{ this.price }}</p>
    <!-- 수량 조절 -->
    <div class="MS_sp">
      <p>
        수량:
        <b-form-spinbutton id="sb-input" v-model="cnt" wrap></b-form-spinbutton>
      </p>
    </div>

    <!-- 총 금액 -->
    <div class="input-group mb-3" style="width: 250px">
      <button
        class="btn btn-outline-secondary"
        type="button"
        @click="increment"
      >
        +
      </button>
      <input
        type="number"
        class="form-control"
        placeholder=""
        aria-label="Example text with two button addons"
        v-model="cnt"
      />
      <button
        class="btn btn-outline-secondary"
        type="button"
        @click="decrement"
      >
        -
      </button>
      <p>총금액: {{ calculateTotal() }}원</p>
      <!-- 총 금액 계산 -->
    </div>
   
    <!-- 담기 버튼 -->
    <button type="button" class="btn btn-primary" data-bs-toggle="modal" data-bs-target="#confirm" @click="confirm">
담기 
</button>
<div class="modal" tabindex="-1" id="confirm" >
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title">Modal title</h5>
            <button
              type="button"
              class="btn-close"
              data-bs-dismiss="modal"
              aria-label="Close"
            ></button>
          </div>
          <div class="modal-body" >
            <p>{{ this.msg }}</p>
          </div>
          <div class="modal-footer" v-if="asking">
            
            <button type="button" class="btn btn-primary"   @click="confirmIncrease_cnt ">장바구니에 담기</button>
            <!-- <button type="button" class="btn btn-primary" v-else  @click="addToCart ">장바구니에 담기</button> -->
          </div>
        </div>
      </div>
    </div>
    <!-- <button type="button" data-bs-target="#confirm">담기</button> -->
  </div>
</template>
  
  <script>
import axios from "axios";
// import { mapGetters, mapMutations } from "vuex";
import cartStore from "@/store/modules/C_CartStore";
export default {
  computed: {
    cart() {
      return cartStore.state.cartList;
    },
  },
  name: "C_MenuInf",
  data() {
    return {
      fnum: null,
      nfNum: null,
      selected: "",
      cnt: 1,
      menu: [],
      menuInfo: {},
      price: 0,
      option: 0,
      asking:false,
      msg:"장바구니에 담겼습니다"
    };
  },
  methods: {
    confirm(){
      console.log(this.menuInfo.option + "option")
      console.log(this.menuInfo.fnum + "num")
      let curr = this.menuInfo.fnum + this.option;
      // let curr = this.menuInfo.fnum; // 현재 상품의 fnum 가져오기
      console.log(curr);
      let y = this.$store.state.cartStore.cartList.some(
        (item) => item.osfnum === curr
        );
      if(y){
        this.msg="이미 담겨있는 상품입니다"
        this.asking = true;
      }else{
        console.log(curr);
        console.log(y +"???")
        // this.msg="장바구니에 담았습니다"
        this.addToCart();
      }
    },

    confirmIncrease_cnt(){
      let curr = this.menuInfo.fnum + this.option;
      // this.$store.commit("cartStore/INCREASE_CNT", curr);
      this.$store.commit("cartStore/INCREASE_CNT", {
        osfnum: curr,
        fcnt :this.cnt
      });
      console.log(curr);
      this.msg ="장바구니에 담겼습니다"
      this.asking =false;
    },

    increment() {
      this.cnt++;
    },
    decrement() {
      if (this.cnt > 1) {
        this.cnt--;
      }
    },
    async fetchData(fnum) {
      await axios
        .get("/item/itemView", {
          params: {
            f: fnum,
          },
        })
        .then((res) => {
          this.menuInfo = res.data;
          this.price = parseInt(this.menuInfo.fprice);
        })

        .catch((error) => {
          console.error("Error fetching menu data:", error);
        });
    },
    calculateTotal() {
      this.totalPrice = this.menuInfo.fprice;
      this.price = parseInt(this.menuInfo.fprice) + parseInt(this.option);
      this.totalPrice = this.price * this.cnt;
      return this.totalPrice;
    },
    addToCart() {
      // let curr = this.menuInfo.fnum; // 현재 상품의 fnum 가져오기
      let curr = this.menuInfo.fnum + this.option;
      this.$store.commit("cartStore/ADD_CART_LIST", {
        fname: this.menuInfo.fname,
        fnum: this.menuInfo.fnum,
        osfnum: curr,
        foption: this.option,
        fprice: this.price,
        ftotal: this.totalPrice,
        fcnt: this.cnt,
      });
      // }
    },
  },
  created() {
    this.fetchData(this.$route.query.f);
  },
};
</script>
  
